













































.listing {
    font-family: 'Saira', sans-serif;
    .wrapper {
        box-shadow: 0px 0px 9px 0px #0000008a;
        border-radius: 7px;
        padding: 20px;
        margin-bottom: 20px;
        background: white;
        text-align: left;
        h5 {
            text-transform: capitalize;
            margin: 0;
            font-size: 1.2em;
            line-height: 0.9em;
        }
        p {
            margin: 0;
        }
        .title {
            font-size: 0.9em;
            color: gray;
            margin-bottom: 15px;
        }
        .statContainer {
            margin-bottom: 20px;
            display: inline-block;
            width: 50%;
            .statHeader {
                font-weight: bold;
                margin-bottom: 5px;
            }
            .stats {
                font-family: "Share Tech Mono", monospace;
                font-weight: bold;
                font-size: 1.8em;
                padding-left: 27px;
            }
            .far {
                font-size: 1.3em;
                min-width: 23px;
            }
            .fa-heart-square {
                color: #eb5858;
            }
            .fa-comments {
                color: #4a90cb;
            }
        }
    }
}
.bounce-enter-active {
    animation: bounce-in .5s;
}
.bounce-leave-active {
    animation: bounce-in .5s reverse;
}
@keyframes bounce-in {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}
